.textBlock{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 3rem;
}

@media (max-width:800px){
    .textBlock{
        margin-bottom: 8rem;
    }
}

@media (max-width:600px){
    .textBlock{
        margin-bottom: 12rem;
    }
}

.outer{
   position: relative;
   z-index: 3;
}

.inner{
    position: relative;
    z-index:8000;
    left:1.3rem;
    bottom:3.8rem;
    /* transform: translate(-50%,-50%); */
}

@media (max-width:540px){
    .textBlock{
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }
    .inner{
        position: relative;
        z-index:8000;
        left:-3.5rem;
        bottom:-0rem;
        /* transform: translate(-50%,-50%); */
    }
    .text{
        margin-top: 2rem;
    }
}

.text{
    margin-left: 3rem;
}

.heading{
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 1.6rem;
    line-height: 124.5%;
    color: #5E6282;
    margin:0;
}

.subHeading{
    font-family: Poppins;
    font-style: normal;
    margin-top: .8rem;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 124.5%;
    color: #5E6282;
    
    height: 4rem;
}