.OuterCard{
    max-width: 37rem;
    min-height: 40rem;
    background: #FFFFFF;
    box-shadow: -1rem -.2rem 10rem .5rem rgba(65, 82, 202,.05),1rem -2rem 4rem 2rem rgba(89, 177, 230,.2);
    border-radius: 2.6rem;
    overflow: hidden;
    padding:2rem 2.5rem;
    margin-top: 4rem;
    margin-left: 4rem;
}

@media (max-width:1000px){
    .OuterCard{
        margin-left: 0;
    }
}

.image{
    max-width: 32rem;
    min-height: 18rem;
    background-image: url("../../images/girl.jpg");
    background-size: cover;
    background-position: center;
    border-radius: 2.4rem;
    overflow: hidden;
    margin-bottom: 2rem;}

.heading{
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 124.5%;
    letter-spacing: 0.015em;
    color: #080809;
}

.date{
    font-family: Poppins;   
    font-style: normal;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 124.5%;
    letter-spacing: -0.005em;
    color: #84829A;
}

.blank{
    width: 1.8rem;
    margin-right: 1rem;
    height: 0;
    background: #84829A;
    border: 1px solid #84829A;
    transform: rotate(90deg);
}

.icon{
   display: flex;
   justify-content: flex-start;
   margin-top: 1rem;
}

.border{
    background: #F5F5F5;
    border-radius: 100%;
    width:3rem;
    height:3rem;
    margin-right: 2rem;
    position: relative;
}

.mainIcon{
    position:absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    font-size: 1.5rem;
    color:#84829A;
}

.info{
   display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
}

.lowerIcon1,.lowerIcon2{
    font-size: 2rem;
}

.lowerIcon1{
    color: #84829A;
}
.lowerIcon2{
    color: #4152CA;
}

.lowerIcon2:hover{
    color: red;
    fill:red;
}


.text{
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 124.5%;
    letter-spacing: -0.02em;
    color: #84829A;
    margin-right: 12rem;
}


@media(max-width:500px){
    .lowerIcon1{
        margin-right: 3rem;
    }
    .text{
        width:160%;
    }
}