.Subscribe{
   max-width:120rem;
   min-height:40rem;
   background: rgb(223, 215, 249,.2); 
   border-radius: 129px 20px 20px 20px;
   /* transform: matrix(-1, 0, 0, 1, 0, 0); */
   margin:2rem 15rem;
   overflow: visible;
   position: relative;
   z-index: 2;
   margin-bottom: 6rem;
}

@media (max-width:950px){
    .Subscribe{
        min-height: 50rem;
        margin: 2rem 3rem;
    }
}


@media (max-width:800px){
    .Subscribe{
        max-width: 70rem;
    }
}

@media (max-width:650px){
    .Subscribe{
        min-height: 58rem;
    }
}

@media (max-width:500px){
    .Subscribe{
        min-height: 65rem;
        margin-top: 10rem;
    }
}

.circle1{
    position: absolute;
    z-index:100;
    left:0;
    bottom:0;
    opacity: .2;
}

.circle2{
    position: absolute;
    z-index:100;
    top:0;
    right:0;
    opacity: .8;
}

.design{
    width:7rem;
    height:7rem;
    background: linear-gradient(201.65deg, #747DEF 10.27%, #5E3BE1 100%);
    border-radius: 100%;
    position: relative;
    left:96%;
    z-index: 500;
    bottom:3rem;
}

@media (max-width:500px) {
    .design{
        width:4rem;
        width:4rem;
        left:92%;
    }
    .icon{
        font-size:.5rem;
    }
}

.icon{
    position:absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    z-index: 600;
    font-size: 3rem;
    color:#fff;
}

