.Card{
    width: 27rem;
    height: 31rem;
    background-color: #fff;
    border-radius: 3.6rem;
    text-align: center;
    padding:4rem 5rem;
    transition: all .2s;
    position: relative;
}

@media (max-width:1200px){
    .Card{
        margin-bottom: 6rem;
    }
}

@media (max-width:900px){
    .Card{
        width: 30rem;
        height: 35rem;
        padding:2rem 3rem;
    }
}

.Card::before{
    position: absolute;
    top:0;
    left:0;
    content:"";
    /* background-color: #DF6951; */
    height:100%;
    width:100%;
    background-image: linear-gradient(to right bottom ,  #fff ,#fff);
    clip-path: circle(2rem at 2rem 2rem);
    transition: all 1s;
    border-radius: 1rem;
}

.Card:hover{
    transform: translateY(-.4rem);
     box-shadow: 1rem 1rem 2rem rgb(223, 105, 81,.2); 
    /* box-shadow: 1rem 1rem 2rem rgba(0,0,0,.2); */
}

@media (max-width:900px){
    .Card::before{
        background-image: linear-gradient(to right bottom ,  #DF6951 ,#fff);
        clip-path: circle(8rem at 2rem 2rem);
    }
    .Card{
        box-shadow: 1rem 1rem 2rem rgb(0, 0, 0,.2); 
    }
}

.Card:hover.Card::before{
    background-image: linear-gradient(to right bottom ,  #DF6951 ,#fff);
    clip-path: circle(8rem at 2rem 2rem);
}

.image{
   margin:1.5rem;
   position: relative;
}

.heading{
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 2rem;
    line-height: 2.7rem;
    text-align: center;
    color: #1E1D4C;
}

.subHeading{
    font-family: Poppins;
font-style: normal;
font-weight: 500;
font-size: 1.6rem;
line-height: 2.6rem;
text-align: center;
color: #5E6282;

}