.InnerCard{
    max-width: 26rem;
    height: 13rem;
    border-radius: 18px;
    background-color: #fff;
    box-shadow: 1rem -1rem 5rem .2rem rgb(89, 177, 230,.1);
    padding:1.5rem;
    display: flex;
    align-items: flex-start;
    position: relative;
    bottom:22rem;
    left:24rem;
    z-index: 2000;
}

@media (max-width:600px){
    .InnerCard{
        bottom:2rem;
        left:8rem;
    }
}

.image{
    width: 5rem;
    height: 5rem;
    background-color: #000;
    border-radius: 100%;
    margin-right: 1.5rem;
    background-image: url("../../images/taj.png");
    background-size: cover;
    background-position: center;
}

@media (max-width:500px){
    .image{
        width:4rem;
        height:4rem;
    }
}

.text{
    width:auto;
}

.subTitle{
    font-family: Poppins;
    margin:0;
    font-style: normal;
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 124.5%;
    letter-spacing: -0.035em;
    color: #84829A;
}

.heading{
    margin-top:.5rem;
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 124.5%;
    letter-spacing: -0.015em;
    color: #080809;
}

.number{
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 124.5%;
    letter-spacing: -0.055em;
    color: #8A79DF;
}

.outerBar{
    max-width: 15.6rem;
    height: 5px;
    background: #F5F5F5;
    border-radius: 3rem;
    position: relative;
}

.innerBar{
     position: absolute;
    top:0;
    left:0; 
    width: 7rem;
    height:100%;
    background: #8A79DF;
    border-radius: 3rem;
     
}