.RightContent{
   position: relative;
   width:50%;
}

@media(max-width:1100px){
    .RightContent{
      width:100%;
    }
    .mainImage{
        height:80%;
    }
}

.mainImage{
    height:auto;
    width:100%;
    display: inline-block;
    text-align:right;
    z-index: 1000;
    position: relative;
}



 .plane{
    position: absolute;
    display: inline-block;
    top:11%;
    left:14%;
    z-index: 2000;
} 

.plane2{
    position: absolute;
    display: inline-block;
    right:1%;
    top:12%;
}

@media(max-width:1400px){
   .plane2,.plane{
      display: none;
   }
}

@media(max-width:1300px){
    .mainImage{
        height:115%;
        width:100%;
    }
 }