.buttons{
   display: flex;
   justify-content: space-between;
   padding:0 7rem;
}

.rectButton{
    width:17rem;
    height:6rem;
    padding:1rem 1.2rem;
    border-radius: 1rem;
    border-color: none;
    color:#fff;
    font-size: 1.8rem;
    text-align: center;
    background: #F1A501;
    border:1px solid #F1A501;
    box-shadow: 0px 20px 35px rgba(241, 165, 1, 0.15);
    transition: all .2s;
}

@media (max-width:1100px){
    .rectButton{
        width:20rem;
    }
}

@media (max-width:600px){
    .rectButton{
        width:25rem;
        height:10rem;
    }
}

.rectButton:hover{
    transform: translateY(-3px);
    box-shadow: 10px 10px 35px rgba(241, 165, 1, 0.65);
}

.image{
   display: inline-block;
   position: relative;
   top:-1rem;
}