.slide{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 6rem 0;
    padding:0 14rem;
    align-items: center;
    position: relative;
}

@media (max-width:1400px){
    .slide{
        padding:0; 
        margin:6rem auto;
    }
}

@media (max-width:1050px){
    .slide{
        padding:0;
        flex-direction: column;
        align-items: center;
    }
}

.spring{
    position: absolute;
    right:9rem;
    z-index: -5;
}

@media (max-width:1400px){
    .spring{
        display: none;
    }
}