.Card{
    width: 31rem;
    height: 46rem;  
    overflow: hidden;
    box-shadow: 0 1rem 2rem rgba(0,0,0,0.2);
    border-radius: 2.4rem;
    z-index: 20;
    position: relative;
    transition: all .2s;
}

.Card:hover{
    transform: translateY(-.3rem);
    box-shadow: 1rem 1rem 2rem 1rem rgb(223, 105, 81,.2); 
}


@media (max-width:1100px){
  .Card{
    margin-bottom: 6rem;
    width: 42rem;
    height: 57rem;
  }
}

@media(max-width:700px){
    .Card{
        max-width: 31rem;
    }
}

