.MainPage{
    background-image: url("../../images/Decore.png");
    background-position: right;
    background-size: cover;
    height:100vh;
    width: 100vw !important;
    padding:2rem 10rem;
    overflow-x: hidden;
}

@media (max-width:600px){
    .MainPage{
        padding:2rem;
    }
}

.content{
    display: flex;
}

@media(max-width:1100px){
    .content{
        display:block;
        text-align: center;
    }
 }

 @media(max-width:1100px){
    .MainPage{
        padding:1rem 4rem;
    }
 }