.Text{
    max-width: 86rem;
    min-height: 11rem;
    margin:2rem auto;
}

@media (max-width:1300px){
    .Text{
        margin:0rem 4rem; 
        margin-bottom: 4rem;
    }
    .heading{
        font-size: 2rem;
    }
}

@media (max-width:950px){
    .Text{
        margin-bottom: 2rem;
        min-height: 17rem;
    }
}

.heading{
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 3.3rem;
    line-height: 54px;
    text-align: center;
    color: #5E6282;

}