.leftContent{
    text-align: left;
    padding:0 8rem;
    width:50%;
}



@media (max-width:1200px){
    .leftContent{
        padding:0 2rem;
        
    }
    .heading{
        font-size:3.5rem ;
        max-width:35rem;
        margin-bottom: 10rem;
    }
}

.heading{
    font-family: Volkhov;
    font-style: normal;
    font-weight: bold;
    font-size: 5rem;
    line-height: 64px;
    text-transform: capitalize;
    color: #14183E;
    width: 50rem;
    height: 13rem;
}

@media (max-width:1000px){
    .leftContent{
        width:70%;
    }
}

@media (max-width:750px){
    .heading{
        margin-left: -10rem;
        font-size: 4rem;
    }
}

@media (max-width:600px){
    .leftContent{
        width:100%;
    }
    .heading{
        margin-left:-5rem;
        font-size: 2.5rem;
    }
}

@media (max-width:500px){
    .leftContent{
        width:140%;
    }
}

.subHeading{
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 2rem;
    line-height: 2.7rem;
    text-align: center;
    color: #5E6282;
    text-align: left;
}