.nav{
   display:flex;
   justify-content: space-between;
}



.navList{
   list-style: none;
   display: flex;
   justify-content: space-between;
   font-size: 2rem;
}

.navItem{
   margin:0 2.8rem;
   padding:.5rem .8rem;
   transition: all .2s;
}

@media (max-width:900px){
    .navItem,.navSelect{
        display: none;
    }
    .navImage{
        margin-top: 2rem;
    }
}

.navItem:hover{
    border:.2rem solid #000;
    border-radius: 10%;
}

.navImage{
    margin-right: 28%;
}

.navSelect{
    list-style: none;
    border:none;
    background-color:transparent;
    font-size: 2rem;
    margin-right: 1.5rem;
}

@media(max-width:1500px){
    .navSelect{
        margin-right: -5rem;
    }
    .navImage{
        margin-right: 22%;
    }
}

@media(max-width:1400px){
    .navItem{
        margin:0 1.5rem;
        padding:.5rem .4rem;
        transition: all .2s;
     }
}

@media(max-width:1100px){
    .navImage{
        margin-right: 1%;
        margin-left: -2%;
    }
    .navItem{
        margin:0 1.2rem;
        padding:.2rem .5rem;
        transition: all .2s;
     }
     .navList{
        flex-wrap: wrap;
     }
     .navSelect{
         margin-right: 3rem;
     }
}