.LeftContent{
    margin-top: 8rem;
    text-align: left;
    width:50%;
}

@media(max-width:1100px){
    .LeftContent{
        margin-top: 8rem;
        text-align: center;
        width:100%;
    }
}

.topHeading{
    margin-top:0;
   padding:0;  
   font-size: 2rem;
   font-weight: 700;
   color:#DF6951;
   text-transform: uppercase;
   line-height: 3rem;
}

.mainHeading{
    margin:0;
    margin-top:1.5rem;
   padding:0; 
   font-size: 8.4rem;
   font-weight:bolder;
   line-height: 8.9rem;
   color:#181E4B;
   font-family: Volkhov;
}

@media (max-width:600px){
    .mainHeading{
        font-size: 5rem;
    }
}

.subHeading{
    margin-top:1.5rem;
    padding:0; 
    font-size: 2.2rem;
    font-weight: 700;
    line-height: 3rem;
    color: #5E6282;
    font-family: Poppins;
}